	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Useless Spiegel","date":"10 May 2004","slug":"/2004-05-10-useless-spiegel/","tags":"Y2004"},"html":"<p>One thing I noticed while on vacation was how utterly useless <a title=\"SPIEGEL ONLINE\" href=\"http://www.spiegel.de/\">Spiegel Online</a> is for keeping up with German news. Take this evening, for example. Of the 5 'Top Stories', 3 are on the US and Iraq (if I want to read Bush-bashing, I'll go closer to the source), 1 is about sports (Oliver Kahn), and 1 about German news (Finance Minister Eichel). Even under 'Politics' there is little serious domestic news (2 of 7 stories; taxing brothels does not qualify as serious). While other <a title=\"Davids Medienkritik\" href=\"http://medienkritik.typepad.com/blog/\">blogs</a> complain about alleged bias in the German media, I would be happy if the premier German news site simply could be somewhat more <em>relevant</em>.</p>"},"next":{"fields":{"title":"Up In The Air","date":"10 May 2004","slug":"/2004-05-10-up-in-the-air/"}},"previous":{"fields":{"title":"Don Park's Daily Habit - Tortures for Dummies","date":"11 May 2004","slug":"/2004-05-11-don-parks-daily-habit-tortures-for-dummies/"}}},{"node":{"fields":{"title":"Up In The Air","date":"10 May 2004","slug":"/2004-05-10-up-in-the-air/","tags":"Y2004"},"html":"<p>We're now a month into the process of registering Christopher for kindergarten, and we are more confused than ever. As you recall, he is behind in his speech development, and it has been recommended to us several times that he not join a regular kindergarten group. There is a local 'integrated' group that includes sessions of speech therapy each week, and there is a 'therapy' group in the county seat Winsen (over 30 km away) that is intensive speech therapy. However, our speech therapist today told us to choose carefully... that the therapy group does not necessarily include more therapy than an integrated group. In any case, the diagnosis has to be confirmed by the county health office, and our appointment there is not until June.</p>\n<p>Either group will be half-days, and will not be compatible with 2 full time working parents. I've informed my employer of the situation, and that I'll be either requesting part-time or leaving completely (and going freelance), but until we have more details, I can't make any decision.</p>"},"next":{"fields":{"title":"","date":"10 May 2004","slug":"/2004-05-10-ongoing-torture/"}},"previous":{"fields":{"title":"Useless Spiegel","date":"10 May 2004","slug":"/2004-05-10-useless-spiegel/"}}},{"node":{"fields":{"title":"","date":"10 May 2004","slug":"/2004-05-10-ongoing-torture/","tags":"Y2004"},"html":"<p><a title=\"ongoing · Torture\" href=\"http://tbray.org/ongoing/When/200x/2004/05/08/Torture\">Tim Bray</a>: \"But if I'd had to write a one-liner as to why (I though the war in Iraq was a good idea), it would have been something along the lines of 'to stop the torture and brutality in the Baghdad hellholes.' Well, so much for that.\"</p>"},"next":{"fields":{"title":"Nena was Emilia...","date":"09 May 2004","slug":"/2004-05-09-nena-was-emilia/"}},"previous":{"fields":{"title":"Up In The Air","date":"10 May 2004","slug":"/2004-05-10-up-in-the-air/"}}}],"pathPrefix":"","first":false,"last":false,"index":490,"pageCount":1101}}

